<template>
  <div>
    <div class="food-list">
      <div class="list__title">
        <i class="fad fa-clipboard-list"></i>
        Danh sch nhm thc n
      </div>
      <div class="list-body">
        <div class="list__option">
          <button @click="_handleButtonAddnew" class="btn-default-green" v-if="categoryAddnew === null">
            To mi nhm thc n
          </button>
        </div>
        <table v-if="(categories && categories.length > 0) || (categoryAddnew !== null)" class="list__table">
          <thead>
          <tr>
            <th>
              STT
            </th>
            <th>
              Tn
            </th>
            <th>
              Mc  u tin
            </th>
            <th>
              M t
            </th>
            <th>
              La chn
            </th>
          </tr>
          </thead>
          <tbody v-if="categories !== null">
          <tr>

            <template v-if="categoryAddnew !== null">
              <td></td>
              <td>
                <input type="text" placeholder="Nhp tn nhm thc n" :maxlength="100"
                       v-model="categoryAddnew.categoryName">
              </td>
              <td>
                <select :defaultvalue="1" v-model="categoryAddnew.priority">
                  <option :value="1">u tin nht</option>
                  <option :value="2">u tin va</option>
                  <option :value="3">Bnh thng</option>
                  <option :value="4">u tin thp</option>
                  <option :value="5">Thp nht</option>
                </select>
              </td>
              <td>
                <textarea placeholder="Nhp m t" :maxlength="200"
                          v-model="categoryAddnew.description"></textarea>
              </td>
              <td>
                <div class="table__option table__option-inline">
                  <button @click="_handleButtonAddnewSave"
                          class="btn-default-green btn-xs table__option--link">
                    <i class="fas fa-check"></i>
                  </button>
                  <button @click="_handleButtonDisableAddnew"
                          class="btn-default-green btn-xs btn-gray table__option--delete">
                    <i class="far fa-times"></i>
                  </button>
                </div>
              </td>
            </template>
          </tr>
          <tr v-for="(category, key, index) in categories"
              :key="key">
            <td>
              {{ key + 1 }}
            </td>
            <td>
              <span v-if="!categoryEdit || categoryEdit.categoryIndex !== key">{{ category.categoryName }}</span>
              <input v-if="categoryEdit && categoryEdit.categoryIndex === key" type="text"
                     placeholder="Nhp tn nhm thc n"
                     :maxlength="150" v-model="categoryEdit.categoryName">
            </td>
            <td>
              <span v-if="(!categoryEdit || categoryEdit.categoryIndex !== key) && category.priority !== null">
                <template v-if="category.priority === 1">u tin nht</template>
                <template v-if="category.priority === 2">u tin va</template>
                <template v-if="category.priority === 3">Bnh thng</template>
                <template v-if="category.priority === 4">u tin thp</template>
                <template v-if="category.priority === 5">Thp nht</template>
              </span>
              <select v-if="categoryEdit && categoryEdit.categoryIndex === key"
                      v-model="categoryEdit.priority">
                <option :value="1">u tin nht</option>
                <option :value="2">u tin va</option>
                <option :value="3">Bnh thng</option>
                <option :value="4">u tin thp</option>
                <option :value="5">Thp nht</option>
              </select>
            </td>
            <td>
              <span v-if="!categoryEdit || categoryEdit.categoryIndex !== key">{{ category.description }}</span>
              <textarea v-if="categoryEdit && categoryEdit.categoryIndex === key" placeholder="Nhp m t"
                        :maxlength="200" v-model="categoryEdit.description"></textarea>
            </td>
            <td>
              <div v-if="!categoryEdit || categoryEdit.categoryIndex !== key"
                   class="table__option table__option-inline">
                <button @click="_handleButtonEnableEdit(category, key)"
                        class="btn-default-green btn-xs btn-yellow table__option--link">
                  <i class="fas fa-edit"></i>
                </button>
                <button @click="_handleButtonDeleteClick(category)"
                        class="btn-default-green btn-xs btn-red table__option--delete">
                  <i class="fas fa-trash-alt"></i>
                </button>
              </div>
              <div v-if="categoryEdit && categoryEdit.categoryIndex === key" class="table__option table__option-inline">
                <button @click="_handleButtonSaveClick(category)"
                        class="btn-default-green btn-xs table__option--link">
                  <i class="fas fa-check"></i>
                </button>
                <button @click="_handleButtonDisableEdit(category)"
                        class="btn-default-green btn-xs btn-gray table__option--delete">
                  <i class="far fa-times"></i>
                </button>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
        <div v-else class="text-center">
          Danh sch trng
        </div>
        <div v-if="pagination.total && pagination.total > 1"
             class="list__pagging">
          <button v-for="(item, key) in pagination.total" :key="key"
                  @click="(key + 1 == pagination.page) ? null : _handlePaggingButton(key + 1)"
                  :class="['pagging-item', (key + 1 === pagination.page) ? 'active' : '']">
            {{ key + 1 }}
          </button>
        </div>
        <b-alert class="mt-4" v-model="formError.isShow" variant="danger" dismissible>
          <ul class="mb-0" v-if="formError.list.length > 0">
            <li v-for="(item, key) in formError.list" :key="key">
              {{ item }}
            </li>
          </ul>
        </b-alert>
      </div>
    </div>
  </div>
</template>

<script>
import {check_null, isLostConnect} from "../../../static";

export default {
  data() {
    return {
      categories: null,
      categoryIndex: 0,
      categoryAddnew: null,
      categoryEdit: null,
      pagination: {
        total: 0,
        page: 1
      },
      formError: {
        list: [],
        isShow: false
      },
    };
  },
  created() {
    this.initCategories();
  },
  methods: {
    initCategories() {
      let page = (this.pagination.page) ? this.pagination.page : 1;
      this.$store.dispatch('openLoader');
      this.$store.dispatch('getPaginationCategories', page)
        .then(({data}) => {
          this.categories = data.result;
          this.pagination.total = data.totalPages;
        }).catch(error => {
        if (!isLostConnect(error)) {

        }
      }).finally(() => {
        this.$store.dispatch('closeLoader');
      })
    },
    _handlePaggingButton(index) {
      this.pagination.page = index;
      this.initCategories();
    },
    numberWithCommas(x) {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    _handleButtonAddnew() {
      if (this.categoryEdit) {
        this.$swal({
          position: 'top-end',
          icon: 'warning',
          title: 'Cha hon thnh Chnh sa nhm thc n',
          showConfirmButton: false,
          timer: 5000,
          toast: true,
        });
      } else {
        this.categoryEdit = null;
        this.categoryAddnew = {
          categoryName: '',
          description: '',
          priority: 1,
        }
      }
    },
    _handleButtonDisableAddnew() {
      this.formError = {
        list: [],
        isShow: false
      }
      this.categoryAddnew = null;
    },
    _handleButtonEnableEdit(category, key) {
      if (this.categoryAddnew) {
        this.$swal({
          position: 'top-end',
          icon: 'warning',
          title: 'Cha hon thnh To nhm thc n mi',
          showConfirmButton: false,
          timer: 5000,
          toast: true,
        });
      } else if (this.categoryEdit) {
        this.$swal({
          position: 'top-end',
          icon: 'warning',
          title: 'Cha hon thnh Chnh sa nhm thc n',
          showConfirmButton: false,
          timer: 5000,
          toast: true,
        });
      } else {
        this.categoryAddnew = null;
        this.categoryEdit = {
          categoryIndex: key,
          ...category
        }
      }
    },
    _handleButtonDisableEdit(key) {
      this.formError = {
        list: [],
        isShow: false
      }
      this.categoryEdit = null;
    },
    _handleButtonAddnewSave() {
      if (this.$store.getters.getLoader) {
        this.$swal({
          position: 'top-end',
          icon: 'warning',
          title: 'ng thao tc qu nhanh',
          showConfirmButton: false,
          timer: 5000,
          toast: true,
        });
      } else {
        this.formError = {
          list: [],
          isShow: false
        }
        if (check_null(this.categoryAddnew.categoryName)) {
          this.formError.list.push('Tn nhm thc n khng c  trng');
          this.formError.isShow = true;
        }
        if (!this.formError.isShow) {
          this.$store.dispatch('openLoader');
          this.$store.dispatch('addNewCategory', this.categoryAddnew)
            .then(response => {
              this.initCategories();
              this._handleButtonDisableAddnew();
              this.$swal('Thnh cng!',
                'Nhm thc n  c cp nht ln h thng.',
                'success')
            }).catch(error => {
            if (!isLostConnect(error, false)) {
              this.$swal({
                title: 'C li xy ra',
                html: 'Vui lng th li',
                icon: 'warning',
                showCloseButton: true,
                confirmButtonText: 'ng',
              });
            }
          }).finally(() => {
            this.$store.dispatch('closeLoader');
          })
        }
      }
    },
    _handleButtonSaveClick() {
      if (this.$store.getters.getLoader) {
        this.$swal({
          position: 'top-end',
          icon: 'warning',
          title: 'ng thao tc qu nhanh',
          showConfirmButton: false,
          timer: 5000,
          toast: true,
        });
      } else {
        let category = this.categoryEdit;
        this.formError = {
          list: [],
          isShow: false
        }
        if (check_null(category.categoryName)) {
          this.formError.list.push('Tn nhm thc n khng c  trng');
          this.formError.isShow = true;
        }
        if (!this.formError.isShow) {
          this.$store.dispatch('openLoader');
          this.$store.dispatch('editCategoryById', category)
            .then(response => {
              this.initCategories();
              this._handleButtonDisableEdit();
              this.$swal('Thnh cng!',
                'Nhm thc n  c cp nht ln h thng.',
                'success')
            }).catch(error => {
            if (!isLostConnect(error, false)) {
              this.$swal({
                title: 'C li xy ra',
                html: 'Vui lng th li',
                icon: 'warning',
                showCloseButton: true,
                confirmButtonText: 'ng',
              });
            }
          }).finally(() => {
            this.$store.dispatch('closeLoader');
          })
        }
      }
    },
    _handleButtonDeleteClick(category) {
      if (this.categoryAddnew) {
        this.$swal({
          position: 'top-end',
          icon: 'warning',
          title: 'Cha hon thnh To nhm thc n mi',
          showConfirmButton: false,
          timer: 5000,
          toast: true,
        });
      } else if (this.categoryEdit) {
        this.$swal({
          position: 'top-end',
          icon: 'warning',
          title: 'Cha hon thnh Chnh sa nhm thc n',
          showConfirmButton: false,
          timer: 5000,
          toast: true,
        });
      } else if (this.$store.getters.getLoader) {
        this.$swal({
          position: 'top-end',
          icon: 'warning',
          title: 'ng thao tc qu nhanh',
          showConfirmButton: false,
          timer: 5000,
          toast: true,
        });
      } else {
        this.$swal({
          title: `Xo nhm thc n?`,
          html: 'Bn c chc chn mun xo.',
          icon: 'warning',
          confirmButtonText: 'Xo',
          confirmButtonColor: '#F05348',
          showCancelButton: true,
          cancelButtonText: 'Hu',
          showCloseButton: true
        }).then((result) => {
          if (result.value) {
            this.$store.dispatch('openLoader');
            this.$store.dispatch('deleteCategoryById', category.categoryId)
              .then(response => {
                this.$swal({
                  position: 'top-end',
                  icon: 'success',
                  title: 'Cp nht danh sch thnh cng',
                  showConfirmButton: false,
                  timer: 5000
                })
                this.initCategories();
              }).catch(error => {
              if (!isLostConnect(error, false)) {
                this.$swal({
                  title: 'C li xy ra',
                  html: 'Vui lng th li',
                  icon: 'warning',
                  showCloseButton: true,
                  confirmButtonText: 'ng',
                });
              }
            }).finally(() => {
              this.$store.dispatch('closeLoader');
            })
          }
        })
      }
    }
  }
}
</script>



